.gpt3__footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background: var(--color-footer);
  margin-top: 10vh;
  margin-bottom: 5vh;
  color: #fff;
}
.gpt3__footer-heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}
.gpt3__footer-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 54px;
  line-height: 75px;
}
.gpt3__footer-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  border: 1px solid #fff;
  text-align: center;
  margin-bottom: 2rem;
  margin-top: 2rem;
  cursor: pointer;
}
.gpt3__footer-btn p {
  font-size: 16px;
  font-family: var(--font-family);
  line-height: 21px;
  color: #fff;
  word-spacing: 2px;
}
.gpt3__blog-link_container {
  display: flex;
  width: 100%;
  flex-direction: row;
  margin: 2rem 0;
}
.gpt3__footer-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: left;
}
.gpt3__footer-links p {
  cursor: pointer;
  margin: 0.5rem 0;
}
.gpt3__footer-links div {
  min-width: 250px;
  margin: 1rem 0;
}
.gpt3__footer-links_div h4 {
  font-size: 16px;
  line-height: 17px;
  font-family: var(--font-family);
  color: #fff;
  margin-bottom: 0.9rem;
}
.gpt3__footer-links_logo {
  display: flex;
  flex-direction: column;
}
.gpt3__footer-links_logo img {
  width: 118px;
  height: 30px;
  margin-bottom: 1rem;
}
@media screen and (max-width: 850px) {
  .gpt3__footer-heading h1 {
    font-size: 44px;
    line-height: 50px;
  }
  .gpt3__footer {
    padding-top: 10vh;
  }
  .gpt3__blog-link_container {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
  }
  .gpt3__footer-links div {
    min-width: 150px;
    margin: 1rem 0;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__footer-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }

  .gpt3__footer-links div {
    margin: 1rem 0;
  }

  .gpt3__footer-btn p {
    font-size: 14px;
    line-height: 20px;
  }
  .gpt3__blog-link_container {
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
  }
}

@media screen and (max-width: 400px) {
  .gpt3__footer-heading h1 {
    font-size: 27px;
    line-height: 38px;
  }
}
