.gpt3__possibilities {
  display: flex;
  flex-direction: row;
}
.gpt3__possibilities-image {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 2rem;
}
.gpt3__possibilities-image img {
  width: 100%;
  height: 100%;
}
.gpt3__possibilities-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}
.gpt3__possibilities-content h4 {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71e5ff;
}
/* same tag name. differnt design n this is how yu pick on children */
.gpt3__possibilities-content h4:last-child {
  color: #ff8a71;
}
.gpt3__possibilities-content h1 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}
.gpt3__possibilities-content p {
  font-family: var(--font-family);

  font-size: 16px;
  line-height: 30px;
  color: var(--color-text);
  margin-bottom: 2rem;
}
@media screen and (max-width: 950px) {
  .gpt3__possibilities {
    flex-direction: column;
  }
  .gpt3__possibilities-image {
    margin: 1rem 0;
  }
  .gpt3__possibilities-content {
    margin: 1rem 0;
  }
  .gpt3__possibilities-content h1 {
    font-size: 30px;
  }
}
